.popover {
    position: absolute;
    left: -180px;
    width: 160px;
    height: 160px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 2px 8px 0 rgb(0 0 0 / 16%);
    padding:10px;
}
.popover::before {
    content: '';
    position: absolute;
    top: 70px;
    right: -20px;
    display: block;
    width: 0;
    height: 0;
    border-left: 10px solid #fff;
    border-top: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid transparent;
}
.popover::after {
    content: '';
    z-index: 0;
    position: absolute;
    top: 0;
    right: -420px;
    display: block;
    width: 420px;
    height: 100%;
}

.popover-btn {
    margin: 20px auto 0;
    display: block;
    width: 120px;
    height: 20px;
    border: none;
    border-radius: 10px;
    background-color: transparent;
    color: #000;
    font-size: 12px;
    font-style: italic;
    cursor: pointer;
}
.popover-btn:hover {
    background-color: #000;
    color: #fff;
}
.popover-btn.disabled:hover {
    background-color: #ccc;
}


.mod {
    display: inline-block;
    height: 160px;
    border: 1px solid #ccc;
    position: relative;
    z-index: 1;
}
.mod.p {
    width: 79px;
    background-color: red;
}
.mod.k {
    width: 159px;
    background-color: blue;
}
.mod.j {
    width: 159px;
